.contacts {
  display: flex;
  flex-direction: column;
  gap: 8px;
  &--hover {
    transition: all 1s;
    @include onTablet {
      &:hover {
        color: $hoverColor;
        transform: scale(1.05);
      }
    }
  }

  &__wrapper {
    display: flex;
    flex-direction: column;
    gap: 48px;
    @include pageGrid;
  }

  &__information {

    display: flex;
    flex-direction: column;
    gap: 24px;
    color: $main-text-color;
    grid-column: 1 / -1;

    @include onTablet {
      grid-column: 4 / -1;
    }

    @include onDesktop {
      grid-column: 8 / 11;
    }

    &__title {
      margin: 0;
      font-family: "Poppins", sans-serif;
      font-size: 14px;
      font-weight: 400;
      line-height: 140%;
    }

    &__text {
      margin: 0;
      font-size: 16px;
      font-weight: 500;
      line-height: 140%;

      &--link {
        color: $main-text-color;
        text-decoration: none;
      }
    }
  }
}
